import React, { useReducer } from 'react';
import CartContext from './CartContext';

// action type
const SHOW_HIDE_CART = "SHOW_HIDE_CART";
const ADD_TO_CART = "ADD_TO_CART";
const REMOVE_ITEM = "REMOVE_ITEM";

// initialState
const initialState = {
  showCart: false,
  cartItems: [], /* cart */
  test: "hi test",
};

// reducer
/* 

/* 
  0311-0540. ADD_TO_CART

  10. cart item  item   find()

  15.  cart   ,  
    + 
  payload  item... spread ... [],  qty:1 

  20. cart  item  , 
  20-10. cart item id === payload  item id  - map()

  30.    cart   , 
  cart item... spread ... [] , qty +1 

  30-10.   cart  ,     return  ()


*/

const CartReducer = (state, action) => {
  switch (action.type) {
    case SHOW_HIDE_CART: {
      return {
        ...state,
        showCart: !state.showCart, /* show & hide Btn */
      };
    }

    /* ...spread operator 
        + new item add */
    case ADD_TO_CART: {
      // check if product is already existing
      const exist = state.cartItems.find((x) => x._id === action.payload._id);

      // incerase qty +1 or return with qty: 1
      if (exist) {
        return {
          ...state,
          cartItems: state.cartItems.map((x) =>
            x._id === action.payload._id
              ? { ...x, qty: x.qty + 1 }
              : x
          ),
        };
      } else {
        return {
          ...state,
          cartItems: [
            ...state.cartItems,
            {
              ...action.payload,
              qty: 1,
            },
          ],
        };
      }
    }

    /* remove: 
      .filter
      item._id     */
    case REMOVE_ITEM: {
      return {
        ...state,
        cartItems: state.cartItems.filter(
          (item) => item._id !== action.payload
        ),
      };
    }

    default:
      return state;
  }
};

const CartState = ({ children }) => {

  // state with dispatch, CartReducer, initialState
  const [state, dispatch] = useReducer(CartReducer, initialState);
  console.log(state.cartItems);

  // dispatch
  const addToCart = (item) => {
    dispatch({ type: ADD_TO_CART, payload: item });
  };

  const showHideCart = () => {
    dispatch({ type: SHOW_HIDE_CART });
  };

  const removeItem = (id) => {
    dispatch({ type: REMOVE_ITEM, payload: id });
  };

  // function
  // totalPrice,
  // .reduce :           .
  // const totalPrice = state.cartItems.reduce((total, item) => total + item.price, 0);
  // const totalPrice = state.cartItems.reduce((total, item) => total + item.qty * item.price, 0).toFixed(2);
  const totalPrice = state.cartItems.reduce((total, item) => total + item.qty * item.price, 0);



  return (
    <CartContext.Provider
      value={{
        showCart: state.showCart,
        cartItems: state.cartItems,
        test: state.test,
        addToCart,
        showHideCart,
        removeItem,
        totalPrice,
      }}
    >
      {children}
    </CartContext.Provider>
  );
};

export default CartState;
